.u-button {
  background: #e0e0e0;
  border: none;
  border-radius: 4px;
  color: #000000;
  position: relative;
  min-width: 72px;
  padding: 5px 13px;
  display: inline-block;
  font-family: "Open Sans", "Helvetica Neue", Arial, "Hiragino Sans GB", "Microsoft YaHei", sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0;
  overflow: hidden;
  will-change: box-shadow, transform;
  outline: none;
  cursor: pointer;
  text-decoration: none;
  text-align: center;
  line-height: 1.57143;
  vertical-align: middle;
}
.u-button::-moz-focus-inner {
  border: 0;
}
.u-button:hover {
  background-color: #eeeeee;
}
.u-button:active {
  background-color: #bdbdbd;
}
.u-button.colored {
  color: #1e88e5;
}
.u-button.colored:focus:not(:active) {
  background-color: #bdbdbd;
}
.u-button.disable {
  -webkit-box-shadow: none;
  filter: alpha(opacity=65);
  box-shadow: none;
  cursor: not-allowed;
  opacity: 0.65;
}
input.u-button[type="submit"] {
  -webkit-appearance: none;
}
.u-button-floating {
  border-radius: 50%;
  font-size: 14px;
  height: 38px;
  margin: auto;
  min-width: 38px;
  width: 38px;
  padding: 0;
  line-height: normal;
  border: 1px solid #e0e0e0;
}
.u-button-floating.colored {
  background: #43a047;
  color: #ffffff;
  background: #ffffff;
}
.u-button-floating.colored:hover {
  background-color: #66bb6a;
}
.u-button-floating.colored:focus:not(:active) {
  background-color: #43a047;
}
.u-button-floating.colored:active {
  background-color: #43a047;
}
.u-button.disabled,
.u-button[disabled] {
  cursor: not-allowed;
  opacity: 0.65;
  filter: alpha(opacity=65);
  -webkit-box-shadow: none;
          box-shadow: none;
